'use client';
import Image from "next/image";
import Link from "next/link";
import React, { useEffect, useState } from 'react';
import UserCard from "./components/userCard";

const Home = () => {
  const [users, setUsers] = useState([]);
  const [search, setSearch] = useState('');
  const [timeoutId, setTimeoutId] = useState(null);
  const [allUsers, setAllUsers] = useState([]); // Состояние для всех пользователей

  // Загрузка пользователей
  useEffect(() => {
    const fetchUsers = async () => {
      const response = await fetch('https://dummyjson.com/users');
      const data = await response.json();
      setUsers(data.users);
      setAllUsers(data.users); // Сохраняем всех пользователей для фильтрации
    };

    fetchUsers();
  }, []);

  const handleSearch = (e) => {
    const value = e.target.value;
    setSearch(value);

    if (timeoutId) {
      clearTimeout(timeoutId);
    }

    const id = setTimeout(() => {
      // Фильтрация пользователей по имени
      const filteredUsers = allUsers.filter(user => 
        user.firstName.toLowerCase().includes(value.toLowerCase()) || 
        user.lastName.toLowerCase().includes(value.toLowerCase())
      );
      setUsers(filteredUsers); 
    }, 1000);

    setTimeoutId(id);
  };

  return (
    <div>
      <input type="text" placeholder="Поиск пользователей" value={search} onChange={handleSearch} />
      <div className="user-list">
        {users.map(user => (
          <UserCard key={user.id} user={user} />
        ))}
      </div>
    </div>
  );
};

export default Home;
